<template>
  <div id="repos">
    <div class="repos-list">
      <v-card class="ma-5" color="success" flat>
        <v-card-title class="text-h6" style="color:white;"> 🤔 请选择一个仓库存储你的想法 (建议使用私有仓库)</v-card-title>
        <v-list dense style="border-radius:0;">
          <v-list-item-group color="success">
            <v-list-item
              v-for="repo in repos"
              :key="repo.id"
              @click.stop="selectRepo(repo.name)"
            >
              <v-list-item-icon>
                <v-icon v-text="visibilityIcon(repo.private)"></v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title v-text="repo.name"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-card>
    </div>
    <div class="dialog">
      <v-dialog v-model="dialog" persistent max-width="290">
        <v-card>
          <v-card-title class="text-h6"> 🤔 你确定用{{this.selectedRepo}}这个仓库收集你的想法吗?</v-card-title>
          <v-card-text>点击确定，配置完成！后续配置页面不再显示，专注记录想法：）</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="red darken-1" text @click="dialog = false">
              再想想
            </v-btn>
            <v-btn color="green darken-1" text @click="saveRepo()">
              确定
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import GitHub from "../api/github";
export default {
  data() {
    return {
      repos: [],
      selectedRepo: "",
      dialog: false,
    };
  },
  computed: {},
  methods: {
    visibilityIcon(private_repo) {
      return private_repo ? "mdi-eye-off" : "mdi-eye";
    },
    async fetchRepos() {
      let user = new GitHub(localStorage.token);
      const res = await user.getRepos();
      this.repos = res.data;
    },
    selectRepo(name) {
      this.selectedRepo = name;
      this.dialog=true
    },
    saveRepo(){
      localStorage.repo=this.selectedRepo
      this.dialog=false
      this.$message.success("配置完毕，即将跳转至写作页……")
      this.$router.push("/")
    }
  },
  async created() {
    this.fetchRepos();
  },
};
</script>
